import React, { Component } from 'react'

class Sample extends Component {
    state = { a : 'Hello'}

    button = () => {
        console.log('Inside click button')
        this.state.a = 'you pressed the button'
        this.setState({a:'you click'})
    }

    render() {
        return (
            <div>
            <h1>{this.state.a}</h1>
            <button type = 'button' onClick = {this.button}>Click me</button>    
            </div>
        )
    }
}

export default Sample
